import "./import/modules";
import "../../node_modules/bootstrap/dist/js/bootstrap.bundle";

const messages = document.querySelector('.messages');
const chatInput = document.querySelector('#chatInput');
const footerForm = document.querySelector('.chat-footer');
const uploadedFile = document.querySelector('#fileid');

const renderMessage = (message) => {
    const divItem = document.createElement('div');

    divItem.classList.add('message_sent');
    divItem.innerHTML = `<div class="message-items">
    <div class="message__item">
        <button class="message__btn"><img src="img/sprites/sprite.svg#icon-more-horizontal"
                alt=""></button>
        <div class="message__text message__text_sent">${message}</div>
        <div class="message__status-icon message__status-icon"></div>
    </div>
</div>`

    messages.append(divItem);
    messages.scrollTop = messages.scrollHeight;
}

const sentMessage = (event) => {
    event.preventDefault();
    let messageValue = chatInput.value;

    renderMessage(messageValue);
    chatInput.value = '';
}

function loadedFiles() {
    let files = uploadedFile.files;
    let fileInfo = '(' + formatBytes(files[0].size) + ') ' + files[0].name;

    fileInfo = `<a href="#" class="message__files-link__sent">${fileInfo}</a>`;
    renderMessage(fileInfo);
}

function formatBytes(bytes, decimals = 2) {
    if (bytes === 0) return '0 B';

    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ['B', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));

    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
}

messages.scrollTop = messages.scrollHeight;
uploadedFile.addEventListener('change', loadedFiles);
footerForm.addEventListener('submit', sentMessage);